@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@300;500;800&display=swap);
body,html,#root {
  margin: 0;
  font-family: 'Poppins';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  width: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{
    height: 100%;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    background-color: rgb(22, 22, 22);
}

button{
    background-color: rgb(197, 153, 8);
    color: black;
    border: none;
    border-radius: 0;
    min-width: 100px;
    min-height: 2rem;
    margin: 0.2rem;
    margin-right: 0;
    transition: all 0.2s;
    font-family: 'Poppins';
    font-weight: 800;

}

button:hover{
    background-color: rgb(238, 185, 11);
    cursor: pointer;
    box-shadow: 0 0 10px rgb(197, 153, 8);
}


#winnerModal{
    position: fixed;
    z-index: 999;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.8);
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

#winnerCont{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    grid-gap:1em;
    gap:1em;
    height: 400px;
    width: 20%;
    background-color: blanchedalmond;
}

#winnerCont>h1{
    text-align: center;
}

#newGame{
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
#join{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

#code{
    margin-bottom: 2rem;
    font-size: 2rem;
    color : white;
    text-align: center;
}

#joinbtn{
    padding: 0.5rem 1.8rem;
    font-size: 1.5rem;
}

#roomId{
    margin-bottom: 1em;
    background-color: transparent;
    outline: none;
    border: 2px solid rgb(197, 153, 8);
    width: 300px;
    font-size: 1rem;
    font-family: 'Poppins';
    color: white;
    padding: 1rem;
}
#chatComp{
    height: 100%;
    width: 25%;
    min-width:300px ;
    
}
#messagesCont{
    height: 70%;
    max-height: 500px;
    border: 1px solid black;
    padding: 2rem;
    border:2px solid rgb(197, 153, 8);
    margin-bottom: 1rem;
    color: white;
}

#form{
    width: 100%;
    display: -webkit-flex;
    display: flex;
}
#form>form{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    height: 3rem;
}

#inpField{
    width:100%;
    margin: 0.2rem;
    margin-left: 0;
    font-family: 'Poppins';
    font-weight: 300;
    color: white;
    background-color: transparent;
    border:2px solid rgb(197, 153, 8);
    outline: none;
    padding: 1rem;
}

#headingChat{
    color: rgb(197, 153, 8);
}

#messages{
    height: 85%;
    max-height: 85%;
    overflow-y: scroll;
    overflow-x: hidden;
}
#gameCont{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width:100%;
    grid-gap:1em;
    gap:1em;
}


#coj{
    -webkit-transform: scale(1.5);
            transform: scale(1.5);
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    grid-gap:1em;
    gap:1em;
    -webkit-align-items: center;
            align-items: center;
}
